import Vue from 'vue';
import Vuex from 'vuex';
import Store from './store/index';
import App from './App';
import router from './router';
import BootstrapVue from "bootstrap-vue";
import BpComponents from './components/index';
import BpDirectives from './directives/index';
import axios from 'axios';
import VueAxios from 'vue-axios';
import Storage from 'vue-ls'
import VueTheMask from 'vue-the-mask';
import Vuelidate from 'vuelidate';

Vue.use(Vuex);
Vue.use(VueAxios, axios);
Vue.use(Store);
Vue.use(BootstrapVue);
Vue.use(BpComponents);
Vue.use(BpDirectives);
Vue.use(Storage);
Vue.use(VueTheMask);
Vue.use(Vuelidate);

Vue.filter('FormatAddress', function (data) {
  if (typeof data === 'undefined') return '';

  const prefixes = {
    street: "",
    house: ".",
    flat: ".",
    entrance: "",
    floor: "",
    code: "",
    comment: ""
  };

  let preparedData = [];

  for (let prop in data) {
    if (prop == 'id' || data[prop] == '') continue;

    let prefix = prefixes[prop] || '';
    let preparedItem = prefix + ' ' + data[prop];
    preparedData.push(preparedItem);
  }

  return preparedData.join(', ');
});

Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
});


